<template>
	<view class="container needDeal">
		<watermark></watermark>
		<!-- 轮播 -->
		<view class="banner_wrap">
			<swiper class="swiper" :indicator-dots="true" :indicator-color="'rgba(255,255,255,0.4)'" :indicator-active-color="'#fff'"
			 :autoplay="true">
				<swiper-item v-for="(item,index) in bannerList" :key="index">
					<image :src="item" mode="aspectFill" style="width: 100%;"></image>
				</swiper-item>
			</swiper>
		</view>
		<!-- 下面部分 -->
		<view class="deal_bottom">
			<view class="dealBox">
				<view class="title">跟进</view>
				<view class="dealList">
					<view class="list_item" v-for="(item,index) in followList" :key="index" @tap="goList(index)">
						<image :src="item.icon" mode=""></image>
						<view class="follName">{{item.name}} <text :class="item.count > 0 ? 'redCount' : ''">({{item.count}})</text></view>
					</view>
				</view>
			</view>
			<view class="dealBox">
				<view class="title">催办</view>
				<view class="dealList">
					<view class="list_item list_over" v-for="(item,index) in overList" :key="index" @tap="goOverList(index)">
						<image :src="item.icon" mode=""></image>
						<view class="follName">{{item.name}} <text :class="item.count > 0 ? 'redCount' : ''">({{item.count}})</text></view>
					</view>
				</view>
			</view>
		</view>
		<tabBar></tabBar>
	</view>
</template>

<script>
	import tabBar from '@/components/tabBar3.vue';
	import watermark from '@/components/watermark.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				isshow:false,
				bannerList: ['/static/img/banner3.png', '/static/img/banner3.png'],
				followList:[{
					name: '预约今日到访',
					icon: '/static/salePerson/index_icon1.png',
					count: 0,
				},{
					name: '今日待回访',
					icon: '/static/salePerson/index_icon2.png',
					count: 0,
				},{
					name: '三日内逾期',
					icon: '/static/salePerson/index_icon3.png',
					count: 0,
				},{
					name: '逾期未跟进',
					icon: '/static/salePerson/index_icon4.png',
					count: 0,
				},{
					name: '公共客户',
					icon: '/static/salePerson/index_icon5.png',
					count: 0,
				}],
				overList:[{
					name: '逾期未认购催办',
					icon: '/static/salePerson/index_icon7.png',
					count: 0,
				},{
					name: '逾期未签约催办',
					icon: '/static/salePerson/index_icon8.png',
					count: 0,
				}],
				followInfo:{}, // 跟进数据
				overInfo:{}, // 逾期数据
			}
		},
		onLoad() {
			this._getList()
			this._getList2()
		},
		methods: {
			_getList(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2026', Object.assign({}, ''), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.followInfo = res.data.data
						that.followList[0].count = that.followInfo.reservationData.count
						that.followList[1].count = that.followInfo.returnVisitData.count
						that.followList[2].count = that.followInfo.day3OverdueData.count
						that.followList[3].count = that.followInfo.overdueData.count
						that.followList[4].count = that.followInfo.publicPoolData.count
					}
				})
			},
			_getList2(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2027', Object.assign({}, ''), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.overInfo = res.data.data
						that.overList[0].count = that.overInfo.urgingSubData.count
						that.overList[1].count = that.overInfo.urgingSignData.count
					}
				})
			},
			change(index) {
				this.current = index;
			},
			// 跳转至跟进列表
			goList(index){
				uni.navigateTo({
					url:'/pages/salemanVisual/needDeal/todayVisit?index=' + index
				})
			},
			// 跳转至催办列表
			goOverList(index){
				uni.navigateTo({
					url:'/pages/salemanVisual/needDeal/notOrdered?index='+ index
				})
			}
		},
		components: {
			tabBar, watermark
		}
	}
</script>

<style lang="scss">
	page{
		height: 100%;
		background: #f6f6f6;
	}
	.container {
		.banner_wrap {
			height: 360rpx;
			.swiper, image{
				height: 360rpx;
			}
		}

		.u-scroll-view {
			border-bottom: 1rpx solid #eee;
		}

		.deal_bottom {
			padding-bottom: 30rpx;
			.dealBox{
				.title{
					font-size: 36rpx;
					color: #333;
					margin: 40rpx 0 32rpx 0;
					font-weight: bold;
					border-left: 6rpx solid #03958C;
					padding-left: 20rpx;
				}
				.dealList{
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					align-items: center;
					text-align: center;
					margin: 0 30rpx;
					padding: 30rpx 20rpx 0;
					background-color: #fff;
					border-radius: 16rpx;
					.list_item{
						min-width: 210rpx;
						width: 210rpx;
						padding-bottom: 40rpx;
						image{
							width: 68rpx;
							height: 68rpx;
							min-width: 68rpx;
							min-height: 68rpx;
							padding-bottom: 12rpx;
						}
						.follName{
							font-size: 24rpx;
							color: #333;
							.redCount{
								color: #DB0808;
							}
						}
					}
					.list_over{
						min-width: 50%!important;
						width: 50%!important;
					}
				}
				/* 使用伪元素辅助左对齐 */
				.dealList::after {
						content: '';
						flex: auto;
				}
			}
		}
	}
</style>
